.HamburgerLink{
    height: 30px;

}

.youtubeLogo{
    height: 50px
}

.user-logo{
    height: 40px;
}


.container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #fff;
    padding: 10px 20px;
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
  }
  


  .search-container {
    display: flex;
    align-items: center;
    background-color: #f4f4f4;
    border-radius: 30px;
    padding: 5px 10px;
  }
  
  .search-bar { 
    width: 500px;
    flex: 1;
    border: none;
    padding: 10px;
    font-size: 16px;
    background-color: transparent;
  }
  
  .search-bar:focus{
    outline: none;
  }

  .search-bar::placeholder {
    color: #999;
  }
  
  button {
    padding: 12px 20px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 30px;
    cursor: pointer;
    margin-left: 10px;
  }

  button:hover {
    background-color: #0056b3;
  }
  

  @media (max-width: 768px) {
    .container {
      flex-direction: column;
      align-items: flex-start;
      padding: 20px;
    }
  
    .search-container {
      width: 100%;
      margin-top: 10px;
    }
  
    .search-bar {
      width: 100%;
      margin-bottom: 10px;
    }
  }
  

  .sidebar-container{
    flex: 1; /* Sidebar takes up 30-40% of the container width */
    min-width: 10%;
    max-width: 15%;
    background-color: #fff; /* Sidebar background color */
    color: #333; /* Sidebar text color */
    padding: 20px;
  }

  .video-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    flex: 1; /* Main content takes up 60-70% of the container width */
    min-width: 80%;
    max-width: 90%;
    padding: 20px;
}

.body-container{
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    width: 100%;
}

@media (max-width: 768px) {
    .body-container{
        flex-direction: column;
    }
    .sidebar-container{
        display: none;
    }
}


.video-card {
  max-width: 250px;
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  overflow: hidden;
  margin: 10px;
}

.video-card img {
  width: 100%;
  height: auto;
}

.video-info {
  padding: 10px;
  background-color: #fff;
}

.video-info h1 {
  font-size: 16px;
  margin: 0;
  font-weight: bold;
  color: #333;
}

.published-date, .channel-name {
  font-size: 14px;
  color: #777;
  margin: 5px 0;
}

.video-stats {
  display: flex;
  justify-content: space-between;
  padding: 10px;
  background-color: #fff;
}

.like-count, .view-count {
  font-size: 14px;
  color: #777;
}



.video-container {
  position: relative;
  padding-bottom: 56.25%; 
  overflow: hidden;
}

.video-iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
